export const promise = {
  data: {
    name: 'Promise',
    params: 'executor',
    paramsDes:
      'executor resolve  reject  Promiseexecutor  resolve  reject executorexecutor Promisepromiseresolve  reject promisefulfilledrejectedexecutor (/)resolvepromisefulfilledreject promiserejectedexecutorpromise rejectedexecutor',
    return: 'executorPromise.prototype.then   Promise.prototype.catch promise  ',
    attention: 'Promise.length  1 (). Promise.prototype  Promise .',
    description:
      'pending  Promise fulfilled rejectedPromise  then handlers thenonfulfilled  onrejected Function Promisefulfilled then  onfulfilled Promiserejected then  onrejected  ',
  },
  example: `let myFirstPromise = new Promise( function(resolve, reject) {...} /* executor */  )
    myFirstPromise.then(function(successMessage){
      ...
    })

  function myAsyncFunction(url){
    return new Promise((resolve,reject)=>{
      const xhr = new XmlHttpRequest();
      xhr.open('GET',url);
      xhr.onload=()=>resolve(xhr.responseText);
      xhr.onerror=()=>reject(xhr.statusText)
      xhr.send()
    })
  }
  
`,
  handwriting: `class myPromise{
    succeed = null
    failed = null
    state = pending
    
    constructor(fn){
      fn(this.resolved.bind(this),this.rejected.bind(this))
    }

    resolve(result){
      setTimeout(()=>{
        this.state = 'resolved'
        this.succeed(result)
      })
    }

    reject(reason){
      setTimeout(()=>{
        this.state = 'rejected'
        this.failed(reason)
      })
    }
  }
  
  myPromise.prototype.then = function(isSuccess,isFail){
    const t =this
    return new myPromise(function(resolve,reject){
      setTimeout(()=>{
        if(t.state='resolved'){
          resolve(isSuccess(t.succeed))
        }
        if(t.state='rejected'){
          reject(isFail(t.failed))
        }
      })
    })
  }
  `,

  image: false,
}

